}

.burger:before {
  content: "";
  transition: top .3s .3s, transform .3s cubic-bezier(.23, 1, .32, 1);
  top: -6px;
}

.burger:after {
  content: "";
  transition: bottom .3s .3s, transform .3s cubic-bezier(.23, 1, .32, 1);
  bottom: -6px;
}

.burger.active:before {
  content: "";
  transition: top .3s, transform .3s cubic-bezier(.68, -.55, .265, 1.55) .3s;
  top: 0;
  transform: rotate(45deg);
}

.burger.active:after {
  content: "";
  transition: bottom .3s, transform .3s cubic-bezier(.68, -.55, .265, 1.55) .3s;
  bottom: 0;
  transform: rotate(-45deg);
}

.burger.active {
